* {
  box-sizing: border-box;
  font-family: Helvetica;
  color: white;
}

a {  background-color: black;}



.main {
  width: 100%;
  height: 100vh;
  display: flex;
  background-color: black;
}

.frontParts {
  opacity: 1;
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
background-size: 100% 100vh;
}

.half:hover .frontParts {
  opacity: 0.8!important;
}

.menuPart {
  height: "100%";
  width: 50%;
}

.full-cont{
  height: 100%;
  width: 100%;
}

.comp {
    /* background-image: url('./images/sign.jpg'); */
    background-image: url("/images/bg.jpg");
    background-repeat: no-repeat;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
  background-size: 100% 100vh;
}

.bg {

}

.menuPage {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-size: cover !important;
background-repeat:  no-repeat !important;
background-position: center center !important;
}
.menuItemLink {
  border: 2px solid black;
  border-radius: 30px;
  padding: 10px;
  margin: 10px;
  margin: 10px 20%;
  font-size: 1.2em;
  width: 35%;
  max-width: 150px;
}

.menuItemLink:hover, .button-back:hover {
  opacity: 0.8;
  background: purple;
}

.menuItemLink:active, .button-back:active {
  opacity: 0.8;
  background: #541C4C;
}

.menuItemLink div {
  text-align: center;
}

.half {
  width: 50%;
}

.scroll {
  overflow:scroll;
  overflow-y:auto;
  overflow-x:hidden;
}

.about {
  /* height: 80%; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5%;
}

.aboutPhoto {
  height: 150px;
  width: 150px;
}

.about-master {
  height: 20%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  /* border: 3px black solid; */
  border-bottom: 2px white solid;
}

.about-master-text {
  height: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 60%;
  text-align: center;
  padding: 5%;
}

.about-master-photo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 8%;
}

/* .masters-name {
  position: absolute;
} */

#booking-form {
  display: flex;
  flex-direction: column;
}

.booking-page {
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 25px;
}

.booking-page div {
  padding: 10px;
}

.booking-page option {
  color: black;
}

.booking-page form * {
  padding: 5px;
  margin: 5px;
  color: black;

}

.booking-page form textarea{
  min-height: 100px;
}

.contact-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.contact-title span {
  padding: 5px;
  color: #b14fb3;
  /* color: red; */
  font-size: 20px;
}

.contact-title a {
color: #a8b2ed;
}

.button-back{
  position: absolute;
  top: 20px;
  border: 2px solid black;
  border-radius: 30px;
  padding: 10px;
  background-color: black;
}

a {
  text-decoration: none;
}

