.mobile {
    width: 100%;
}

.hero {
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    background: linear-gradient(332.01deg, rgba(128, 0, 128, 0.76) 0%, rgba(0, 0, 0, 0) 91.99%);
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.hero-text {
    display: flex;
    justify-content: center;
    padding-top: 30%;
    align-items: center;
    flex-direction: column;
}

.hero .hero-text h1 {
font-family: 'Yrsa', serif;
font-style: normal;
font-weight: normal;
font-size: 4em;
line-height: 78px;
display: flex;
align-items: center;
text-align: center;
letter-spacing: -0.02em;
text-transform: uppercase;
line-height: 70px;
margin-bottom: 0;
}

.hero .hero-text h3 {
font-family: 'Open Sans', sans-serif;
font-style: normal;
font-weight: normal;
font-size: 0.9em;
line-height: 1px;
}

.hero .hero-text svg {
    margin-top: 35%;
}

.about-mobile {
    background-color: black;
        /* height: 80%; */
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 5%;
        width: 100%;
}

.mobile-header {
    display: flex;
    justify-content: space-between;
    margin-top: 5%;
    width: 90%;
}

.block-column{
    display: flex;
    flex-direction: column;

}

.mobile-header-name {
    text-transform: uppercase;
    font-weight: bold;
    margin-bottom: 1%;
    text-align: right;
}

.main-text {
    display: flex;
    flex-direction: row;
    width: 90%;
    margin-top: 15%;
}


.purple-bold {
font-family: 'Open Sans', sans-serif;
font-style: normal;
font-weight: 300;
font-size: 96px;
line-height: 131px;
/* identical to box height */
text-align: center;
align-self: flex-end;
height: 60px;
color: #800080;
}

.purple-bold:first-child{
    align-self: flex-start;
    position: relative;
    bottom: 50px;
}

.main-text p {
font-family: 'Open Sans', sans-serif;
font-weight: 300;
font-size: 14px;
line-height: 19px;
text-align: center;

color: #FFFFFF;
}

.machine {
    margin-left: 40%;
}

.mobile-portfolio {
    background: black;
    margin-top: 0px;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.mobile-portfolio .mobile-header {
    background: black;
    margin-top: 0px;
    margin-bottom: 40px;
}

.mobile-snap-wrap{
    margin-bottom: 15px;
    margin: 0 auto;
}

.widget {
    margin-bottom: 60px;
}

.mobile-team {
    background: black;
    margin-top: 0px;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.mobile-team .mobile-header {
    background: black;
    margin-top: 0px;
    margin-bottom: 40px;
}

.about-master-mobile{
    display: flex;
    flex-direction: row;
    margin: 10px 5%;
    align-items: center;
}

.mobile-row{
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-master-photo-mobile {
    margin: 10px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.about-master-photo-mobile h5{
    margin: 5px 1px 4px 1px;
    font-size: 0.6em;

}

.artist-mobile-text {


}

.artist-mobile-text h2{
    color: purple;
    font-family: 'Open Sans';
    font-size: 1.2em;
    font-weight: 800;

}

.artist-mobile-text h4{
    color: white;
    font-family: 'Open Sans';
    font-weight: bold;
    text-transform: none;
    font-size: 0.8em;
    margin-bottom: 1px;
}

.artist-mobile-text p{
    color: white;
    font-family: 'Open Sans';
    font-weight: 200;
    font-size: 0.8em;
    margin-top: 1px;
    font-style: lighter;

}

.mobile-contact {
    background: black;
    margin-top: 0px;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-bottom: 40px;
}

.mobile-contact-column{
    width: 85%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.mobile-contact-header{
    margin:0;
    margin-top: 15px;
    font-family: 'Open Sans';
    font-weight: 200;
}

.mobile-contact-main{
    margin:0;
    margin-bottom: 10px;
    font-family: 'Open Sans';
    font-weight: 700;
}

.mobile-map{ 
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}


